	module.exports = {"data":{"site":{"siteMetadata":{"title":"Annalisa Valente"}},"contentfulPageSelectorPanel":{"pages":[{"name":"Brave the Shave","title":null,"slug":"/projects/macmillan","description":"Leading the UX of fundraising products like Brave the Shave, Mindfulness and others."},{"name":"Google","title":"Google","slug":"/projects/google/","description":"Interaction designer contractor in the Google Play Console team at Google UK."},{"name":"The Telegraph","title":null,"slug":"/projects/thetelegraph/","description":"Working with PO and Developers to bring continuous improvements to the newspaper digital experience."},{"name":"MyMail & Nectar","title":null,"slug":"/projects/mymail-and-nectar/","description":"Launch of MyMail and Nectar proposition to collect Nectar points with the DailyMail."},{"name":"Nectar ","title":null,"slug":"/projects/nectar/","description":"Delivering a new personalised user experience on Nectar web and iOS-Android app."},{"name":"Face and Name ","title":null,"slug":"/projects/face-name/","description":"Concept of an android experience that helps educators match faces to names."},{"name":"The Telegraph puzzle ","title":null,"slug":"/projects/the-telegraph-puzzle/","description":"Redesign the experience of the Telegraph puzzle website."},{"name":"Mindfulness Moments","title":"Mindfulness Moments","slug":"/projects/mindfulness-moments","description":"Microsite for Macmillan Cancer Support"},{"name":"New page","title":null,"slug":"new-page","description":"A new description"}]},"contentfulPage":{"slug":"/projects/macmillan","password":"password1","panels":[{"__typename":"ContentfulHeaderPanel","text":"Brave the shave","media":null},{"__typename":"ContentfulContentPanel","content":{"content":"> My role: UX Designer \n\nThe project:\nAcross the UK, thousands of people are getting sponsored to shave their hair for Macmillan Cancer Support. Brave shavers have raised over £16.8 million. I've worked on Brave the shave website that is current live.\n\nOur KPIs was to increase the registrations and to help users to complete the profile without leaving the website delivering the users a delightful user experience.\n\n\nWhat do we know about our users?\n![4](//images.ctfassets.net/wk0hltq7eofw/7wAEkuGY6c8yaIAAGUuw6E/d2d020d29d554f4c5b49d843b177292e/4.jpg)\n\n![5](//images.ctfassets.net/wk0hltq7eofw/6IGSKz7OtUSKQYW8iIE4aG/7af75525f627148b943b95d88d6cee4f/5.jpg)\n\n\n__Creating personas for donors/fundraising:__ \n\nI've then created personas based on data on demographic, marketing segmentations and research.\nClassifying a difference between donor and fundraiser helped us to define the two main journey that our users do in BTS.\n\n![411124-PDVE7E-661](//images.ctfassets.net/wk0hltq7eofw/54upbDOQcMaaEqya2E0qCq/db5fd4a77c1ea0bced03ed894cfd723f/411124-PDVE7E-661.jpg)\n\n![411124-PDVE7E-662](//images.ctfassets.net/wk0hltq7eofw/5thu73zGHm6euwwM8Ocuk2/225a110ddecfab230318f2bf8280cd9a/411124-PDVE7E-662.jpg)\n\n\n\n","childMarkdownRemark":{"html":"<blockquote>\n<p>My role: UX Designer </p>\n</blockquote>\n<p>The project:\nAcross the UK, thousands of people are getting sponsored to shave their hair for Macmillan Cancer Support. Brave shavers have raised over £16.8 million. I've worked on Brave the shave website that is current live.</p>\n<p>Our KPIs was to increase the registrations and to help users to complete the profile without leaving the website delivering the users a delightful user experience.</p>\n<p>What do we know about our users?\n<img src=\"//images.ctfassets.net/wk0hltq7eofw/7wAEkuGY6c8yaIAAGUuw6E/d2d020d29d554f4c5b49d843b177292e/4.jpg\" alt=\"4\"></p>\n<p><img src=\"//images.ctfassets.net/wk0hltq7eofw/6IGSKz7OtUSKQYW8iIE4aG/7af75525f627148b943b95d88d6cee4f/5.jpg\" alt=\"5\"></p>\n<p><strong>Creating personas for donors/fundraising:</strong> </p>\n<p>I've then created personas based on data on demographic, marketing segmentations and research.\nClassifying a difference between donor and fundraiser helped us to define the two main journey that our users do in BTS.</p>\n<p><img src=\"//images.ctfassets.net/wk0hltq7eofw/54upbDOQcMaaEqya2E0qCq/db5fd4a77c1ea0bced03ed894cfd723f/411124-PDVE7E-661.jpg\" alt=\"411124-PDVE7E-661\"></p>\n<p><img src=\"//images.ctfassets.net/wk0hltq7eofw/5thu73zGHm6euwwM8Ocuk2/225a110ddecfab230318f2bf8280cd9a/411124-PDVE7E-662.jpg\" alt=\"411124-PDVE7E-662\"></p>"}}}]}},"pathContext":{"slug":"/projects/macmillan"}}

